
.mask{
    width:100%;
    height:100vh;
    position: relative;
}
h2{
    color:aliceblue;
}

.into-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    object-fit: cover;
    background-position: center;
}

.mask::after{
    content:"";
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    background-color: black;
    opacity: 0.8;
    position: absolute;
}

.hero{
height: 100%;
width: 100%;
}

.hero .content{
    position : absolute;
    transform: translate(-50%, -50%);
    top:50%;
    left:50%;
    text-align: center;
}

.hero .content h1{
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}

.hero .content p{
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: 200;
}

.content .btn{
    margin: 1rem 0.2rem;
}

@media screen and (max-width:640px){
    .hero .content h1{
        font-size: 3rem;
    }

    .hero .content p{
        font-size: 1.4rem;
    }
} 